import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoTipoProduto } from '../../../../models/dtos/ger/grupoTipoProduto';

interface GrupoTipoProdutoEditViewProps extends EditViewProps<GrupoTipoProduto> {}

const GrupoTipoProdutoEditView: React.FC<GrupoTipoProdutoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<GrupoTipoProduto>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Manutenção de Grupo Tipo Produto">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              label="Código"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o código"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe a descriçâo"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoTipoProdutoEditView;
